export default {
    name: "ClearInput",
    props: {
        /* 左侧文本内容 */
        label: {
            type:String,
            default:'',
        },
        /* 左侧文本内容宽度 */
        labelWidth: [Number, String],
        /* 输入框默认值 */
        value: {
            type:String,
            default: '',
        },
        /* 有效值: text number idcard digit */
        type: {
            type:String,
            default: 'text',
        },
        /* 最大输入长度,设置为 -1 的时候不限制,默认不限制 */
        maxlength: {
            type:[Number,String],
            default:-1,
        },
        /* 输入框为空时占位符 */
        placeholder: {
            type:String,
            default:'',
        },
        /* 是否是密码类型 */
        password: {
            type:Boolean,
            default:false,
        },
        /* 指定 placeholder 的样式 */
        placeholderStyle: {
            type:String,
            default:'',
        },
        /* 指定 placeholder 的样式类 */
        placeholderClass: {
            type:String,
            default:'input-placeholder',
        },
        /* 是否禁用 */
        disabled: {
            type:Boolean,
            default:false,
        },
        /* 获取焦点 */
        focus: {
            type:Boolean,
            default:false,
        },
        /* 指定focus时的光标位置 */
        cursor: Number,
        /* 是否禁用清空按钮 */
        disabledClear: {
            type:Boolean,
            default:false,
        },
        /* 唯一标识符,当多个输入框时获取对应输入框的值 */
        tag: {
          type:String,
          default:"input"
        },
    },
    data() {
        return {
            initValue:this.value,
            isClear:false,
            inputLeftMargin: this.labelWidth ? 0 : 20,
        }
    },
    created() {
        this.getInputValue();
    },
    methods: {
        clearInputValue() {
            this.initValue = '';
            this.isClear = false;
            this.getInputValue();
        },
        input(e) {
            this.isClear = this.isShowClear(e);
            this.initValue = e.detail.value;
            this.getInputValue();
        },
        blur(e) {



            setTimeout(() => {
                this.isClear = false;
            }, 200);
            this.getInputValue();
            this.$emit('blur', e);
        },
        onFocus(e) {
            this.isClear = this.isShowClear(e);
            this.getInputValue();
            this.$emit('focus', e);
        },
        confirm(e) {
            this.$emit('confirm', e);
        },
        onKeyboardheightchange(e){
            this.$emit('keyboardheightchange', e);
        },
        isShowClear (e) {
            return e.detail.value.length !== 0;
        },
        getInputValue () {
            this.$emit('getInputValue', {'tag':this.tag, 'value':this.initValue});
        }
    }
}
